import React,{useState} from "react";
import { useStateValue } from "../StateProvider";
import './product.css';


function Product({rating,price,title,imgUrl,id}){

const[heart,setHeart]=useState(0);
let itemHeart='heart-black';
if(heart>0) itemHeart='heart-red';
const [state,dispatch] =useStateValue();
let repeat = (Math.random())*100;
const onCartAdd =()=>{
    setHeart(heart+1);
    dispatch({
      type: 'ON_CART_ADD',
      description:{id:id + repeat,
       rating:rating,
       title:title,
       price:price,
       imgUrl:imgUrl
      }
    })
    repeat++;
  }

return<div className="product">
        <p>{title}</p>
        <div className='price-tag'>
        <p >  <strong>Price: </strong>
            <small>  $ </small>
            <strong>{price}</strong>
        </p>
        <h2  className={itemHeart}>❤</h2>
        </div>
      <div className="product-rating"> 
     
      <strong className="product-rating">  { 
       Array(rating).fill().map((_,i )=>( 
        <small key={i}> ⭐</small> 
       ))}</strong>
       </div>  
       <img className="product-image" src={imgUrl} alt="" />
          <button onClick={onCartAdd}  className="add-to-basket">Add To Basket</button>
     </div>
}
export default Product;